import Square from "./square";
import React, { useState } from "react";
import "./Game.scss";

const Game = () => {
  const [turn, setTurn] = useState("X");
  const [board, setBoard] = useState(new Array(9).fill(null));
  const winner = calculateWinner(board);

  const onClickHandler = (move) => {
    // toggles the turn 
    setTurn(toggleTurn(turn));
  };
  const renderMoves = (move) => {
    // if the square has already been marked or there is already a winner 
    if (board[move] || winner) return;
    setBoard(() => {
      return board.map((i, idx) => {
        return idx === move ? turn : i;
      });
    });
  };
  const resetBoard = () => {
    setBoard(new Array(9).fill(null))
    setTurn('X')
  }

  return (
    <div className="game">
      <div className="game__title">Welcome to TIC TAC TOE</div>
      <div className="game__turn">
        {winner || `It is the turn of player ${turn}`}
      </div>
      <div className="game__board">
        {renderBoard(onClickHandler, board, renderMoves)}
      </div>
      <button className="game__reset" onClick={() => resetBoard()}>Reset</button>

    </div>
  );
};

const renderBoard = (onClickHandler, board, renderMoves) => {
  let squarelist = [];
  for (let i = 0; i < 9; i++) {
    squarelist.push(
      <Square
        key={i}
        name={i}
        onClickHandler={onClickHandler}
        renderMoves={renderMoves}
        board={board}
        calculateWinner={calculateWinner}
      ></Square>
    );
  }
  return squarelist;
};

const toggleTurn = (turn) => {
  return turn === "X" ? "O" : "X";
};

const calculateWinner = (board) => {
  // winning combinations
  let winningMoves = ["012", "345", "678", "036", "147", "258", "048", "246"];
  for (let move of winningMoves) {
    if (
      board[move[0]] &&
      board[move[0]] === board[move[1]] &&
      board[move[0]] === board[move[2]]
    ) {
      alert(`${board[move[0]]} is the winner`);
      return board[move[0]];
    }
  }
  return null;
};

export default Game;
